@import url('https://fonts.googleapis.com/css2?family=Noto+Serif+Thai:wght@100..900&family=Sriracha&display=swap');
*{
  box-sizing: border-box;
  font-family: "Sriracha", cursive;  
} 

body{
  background : rgb(226, 226, 226);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh ;
  margin: 0;
}
.container{
  margin: 30 px auto;
  width: 350 px;
}
.horizontal.menu{
  list-style-type: none ;
  margin: 0 ;
  padding: 0;
  overflow: hidden ;
  background: gray;
  text-align: center;
}
.horizontal.menu li{
  display: inline-block;
}
.horizontal.menu a{
  color: white;
  text-decoration:  none;
  padding: 16 px;
  display: block;
}