label{
display: inline-block;
margin: 10px 0;
}
input {
    border: 2px solid darkgrey;
    border-radius: 3 px;
    display:block ; 
    font-size: 16 px ;
    padding: 10px;
    width: 100%;
}

input:focus{
    outline : none
}
.btn{
    background-color: rgb(180, 0, 0);
    color: white;
    cursor: pointer;
    display: block;
    margin: 10px 0 30px;
    padding: 10px;
    font-size: 16 px;
    width: 100%;
}
